import React, { useState, useCallback } from 'react'
import Joyride, { ACTIONS, EVENTS, STATUS } from 'react-joyride'

import i18n from '../../controller/i18n'

import { INTRODUCTION_STEPS } from '../../config/joyride'
import TooltipComponent from '../../components/Joyride'


/*
 * STYLES
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
}));
 */


i18n.setTranslations(language => require('./i18n/' + language));

export default function JarvisJoyride(props) {
	//const classes = useStyles();
	
	const [run,setRun] = useState(props.run || false);
	const [stepIndex,setStepIndex] = useState(0);
	
	return (
		<Joyride
			callback={state => {
				const { action, index, status, type } = state;
				
				if ([EVENTS.STEP_AFTER, EVENTS.TARGET_NOT_FOUND].includes(type)) {
					setStepIndex(index + (action === ACTIONS.PREV ? -1 : 1));
				}
				else if ([STATUS.FINISHED, STATUS.SKIPPED].includes(status)) {
					// Need to set our running state to false, so we can restart if we click start again.
				}
				
				//
				
				
			}}
			
			continuous={true}
			disableOverlayClose={true}
			run={run}
			
			stepIndex={stepIndex}
			steps={INTRODUCTION_STEPS}
			tooltipComponent={componentProps => TooltipComponent({ ...componentProps, 'setStep': setStepIndex, 'setRun': setRun })}
			/>
	);
}
