import React from 'react'
import clsx from 'clsx'

import i18n from '../controller/i18n'

import Button from '@material-ui/core/Button'
import Card from '@material-ui/core/Card'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'

import CloseIcon from '@material-ui/icons/Close'


import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	button: {
		marginLeft: theme.spacing()
	},
	grow: {
		flexGrow: 1,
	},
	content: {
		'& p': {
			margin: '0 0 18px',
			lineHeight: '1.42857143',
		}
	},
	closeButton: {
		top: theme.spacing(1),
		color: '#9e9e9e',
		right: '8px',
		position: 'absolute'
	},
	closeButtonOnArrow: {
		top: theme.spacing(3),
	},
	CardTitle: {
		display: 'flex',
		margin: 0,
		padding: '16px 24px',
		flex: '0 0 auto',
	},
	CardContent: {
		minWidth: 350,
		padding: '16px 24px',
		borderTop: `1px solid ${theme.palette.divider}`,
		borderBottom: `1px solid ${theme.palette.divider}`,
	},
	CardActions: {
		display: 'flex',
		alignItems: 'center',
		padding: 8,
		justifyContent: 'flex-end',
		flex: '0 0 auto',
	}
}));


const Markdown = require('markdown-it'),
	  md = new Markdown();

const CardTitle = props => {
	const { children } = props;
	const classes = useStyles();
	
	return (
		<div className={classes.CardTitle}>
			{children}
		</div>
	);
}

const CardContent = props => {
	const { children } = props;
	const classes = useStyles();
	
	return (
		<div className={classes.CardContent}>
			{children}
		</div>
	);
}

const CardActions = props => {
	const { children } = props;
	const classes = useStyles();
	
	return (
		<div className={classes.CardActions}>
			{children}
		</div>
	);
}

export default function Joyride(props) {
	const { continuous, index, step, isLastStep, backProps, skipProps, closeProps, primaryProps, tooltipProps } = props;
	const classes = useStyles();
	
	const buttonProps = {
		...primaryProps,
		'onClick': e => {
			step.primaryAction && step.primaryAction(props);
			primaryProps.onClick(e);
		}
	}
	
	const content = typeof step.content === 'string' ? <div className={classes.content} dangerouslySetInnerHTML={{__html: md.render(i18n.t(step.content))}} /> : <div className={classes.content}>{step.content}</div>
	return (
		<Card {...tooltipProps} open={true}>
			<CardTitle>
				<Typography variant="h6" style={{ margin: 'auto 0' }}>{i18n.t(step.title)}</Typography>
				
				<div className={classes.grow} />
				<IconButton classes={{ root: clsx(classes.closeButton, (step.target.indexOf('.') > -1 || step.target.indexOf('#') > -1) && classes.closeButtonOnArrow) }} {...skipProps}>
					<CloseIcon />
				</IconButton>
			</CardTitle>
			<CardContent dividers>
				{content}
			</CardContent>
			{step.buttons !== false && <CardActions>
				{index > 0 && (<>
				<Button className={classes.button} color="secondary" {...backProps}>
					{i18n.t('back')}
				</Button>
				<div className={classes.grow} />
				</>)}
				
				{!step.primaryButton && continuous && (
				<Button className={classes.button} color="primary" variant="contained" {...primaryProps}>
					{i18n.t(isLastStep ? 'close' : 'next')}
				</Button>
				)}
				
				{!step.primaryButton && !continuous && (
				<Button className={classes.button} {...closeProps}>
					{i18n.t('close')}
				</Button>
				)}
				
				{step.secondaryButton && (
				<Button className={classes.button} color="primary" variant="text" onClick={step.secondaryAction && (() => step.secondaryAction(props))}>
					{i18n.t(step.secondaryButton)}
				</Button>
				)}
				{step.primaryButton && (
				<Button className={classes.button} color="primary" variant="contained" {...buttonProps}>
					{i18n.t(step.primaryButton)}
				</Button>
				)}
			</CardActions>}
		</Card>
	);
}